<template>

<div>
    <button @click="goBack">back</button>
    <add-record></add-record>
    <my-table :data="items"></my-table>
    <show-patron-data />
  </div>

  <div>
    <add-record></add-record>
    <my-table :data="items"></my-table>
    <show-patron-data />
  
  </div>
    
</template>

<script>
import MyTable from './MyTable.vue';
import AddRecord from './AddRecord.vue';
import ShowPatronData from "./ShowPatronData.vue";
export default {
  components: {
    MyTable,
    AddRecord,
    ShowPatronData
  },
  data() {
    return {
      items: [],
    };
  },
  methods: {
    goBack() {
      window.location.href = '/staff.html';
    },
  },
  mounted() {
    fetch('/staff/patron/show/all')
      .then(response => response.json())
      .then(data => {
        this.items = data;
      })
      .catch(error => console.error(error));
  },
};
</script>




