<template>
    <div>
      <h2>Add Record</h2>
      <form @submit.prevent="addRecord" style="display: inline-block;">
        <div style="display: inline-block;">
          <label for="userId">User ID:</label>
          <input type="text" id="userId" v-model="userId" style="display: inline-block; margin-right: 10px;" />
        </div>
        <div style="display: inline-block;">
          <label for="password">Password:</label>
          <input type="password" id="password" v-model="password" style="display: inline-block; margin-right: 10px;" />
        </div>
        <div style="display: inline-block;">
          <label for="firstName">First Name:</label>
          <input type="text" id="firstName" v-model="firstName" style="display: inline-block; margin-right: 10px;" />
        </div>
        <div style="display: inline-block;">
          <label for="lastName">Last Name:</label>
          <input type="text" id="lastName" v-model="lastName" style="display: inline-block; margin-right: 10px;" />
        </div>
        <div style="display: inline-block;">
          <label for="email">Email:</label>
          <input type="email" id="email" v-model="email" style="display: inline-block; margin-right: 10px;" />
        </div>
        <div style="display: inline-block;">
          <label for="telephone">Telephone:</label>
          <input type="text" id="telephone" v-model="telephone" style="display: inline-block; margin-right: 10px;" />
        </div>
        <button type="submit">Add Record</button>
      </form>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        userId: "",
        password: "",
        firstName: "",
        lastName: "",
        email: "",
        telephone: "",
      };
    },
    methods: {
      addRecord() {
        const url = `/staff/patron/add/${this.userId}/${this.password}/${this.firstName}/${this.lastName}/${this.email}/${this.telephone}`;
        fetch(url, {method: 'POST',})
          .then((response) => {
            if (response.ok) {
                
              alert("Record added successfully!");
              window.location.reload();
            } else {
              throw new Error(response.statusText);
            }
          })
          .catch((error) => {
            console.error(error);
            alert("Record could not be added.");
          });
      },
    },
  };
  </script>
  


