<template>
    <div>
      <input v-model="userid" placeholder="Enter userid" />
      <button @click="getData">Get Data</button>
      <div v-if="showData" class="popup">
        <div class="content">
          <button @click="closePopup">x</button>
          <h3>{{ data.userid }}</h3>
          <img :src="data.avatar" :alt="data.username" width="50" height="50">
          <p><strong>Username:</strong> {{ data.username }}</p>
          <p><strong>Telephone:</strong> {{ data.telephone }}</p>
          <p><strong>Email:</strong> {{ data.email }}</p>
        </div>
      </div>
      <div v-if="errorMessage" class="popup">
        <div class="content">
          <button @click="closeError">x</button>
          <h3>Error</h3>
          <p>{{ errorMessage }}</p>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        userid: "",
        showData: false,
        data: {},
        errorMessage: "",
      };
    },
    methods: {
      async getData() {
        try {
          const response = await fetch(`/staff/patron/show/${this.userid}`);
          if (!response.ok) {
            throw new Error("Request failed");
          }
          const data = await response.json();
          if (Object.keys(data).length === 0) {
            throw new Error("Cannot find this patron");
          }
          this.data = data;
          this.showData = true;
        } catch (error) {
          console.error(error);
          this.errorMessage = "Cannot find this patron";
        }
      },
      closePopup() {
        this.showData = false;
      },
      closeError() {
        this.errorMessage = "";
      },
    },
  };
  </script>
  
  <style>
  .popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .content {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
  }
  </style>
  


