<template>
    <div>
      <table>
        <thead>
          <tr>
            <th>Userid</th>
            <th>Avatar</th>
            <th>Username</th>
            <th>Telephone</th>
            <th>Email</th>
            <th>Delete</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in itemsOnCurrentPage" :key="item.userid">
            <td>{{ item.userid }}</td>
            <td><img :src="item.avatar" :alt="item.username" width="50" height="50"></td>
            <td>{{ item.username }}</td>
            <td>{{ item.telephone }}</td>
            <td>{{ item.email }}</td>
            <td><button @click="deleteItem(item.userid)">Delete</button></td>
          </tr>
        </tbody>
      </table>
      <div class="pagination">
        <button :disabled="currentPage === 1" @click="currentPage--">Previous</button>
        <input type="number" v-model.number="currentPage" min="1" max="totalPages" style="width: 50px; text-align: center;"/>
        <span>/ {{ totalPages }}</span>
        <button :disabled="currentPage === totalPages" @click="currentPage++">Next</button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      data: {
        type: Array,
        required: true,
      },
      pageSize: {
        type: Number,
        default: 10,
      },
    },
    data() {
      return {
        currentPage: 1,
      };
    },
    computed: {
      totalPages() {
        return Math.ceil(this.data.length / this.pageSize);
      },
      itemsOnCurrentPage() {
        const startIndex = (this.currentPage - 1) * this.pageSize;
        const endIndex = startIndex + this.pageSize;
        return this.data.slice(startIndex, endIndex);
      },
    },
    methods: {
      jumpToPage() {
        if (this.currentPage > 0 && this.currentPage <= this.totalPages) {
          const page = parseInt(this.currentPage);
          this.currentPage = page;
        }
      },
      deleteItem(userid) {
        if (confirm("Are you sure you want to delete this item?")) {
            fetch(`/staff/patron/delete/${userid}` ,{ method: 'delete',})
            //.then(response => response.json())
            .then(response => {
            if (response.ok) {
            alert("delete success");
            window.location.reload();
            } else {
          console.error(response.message);
            }
        } )
        .catch(error => console.error(error));
        }
      },
    },
  };
  </script>
  
  <style>
  table {
    border-collapse: collapse;
    width: 100%;
  }
  
  th,
  td {
    border: 1px solid black;
    padding: 8px;
    text-align: left;
  }
  
  th {
    background-color: #ccc;
  }
  
  .pagination {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  button {
    margin: 0 10px;
    cursor: pointer;
  }
  </style>


